.header {
  background-color: #282828;
  -webkit-box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5);
  position: fixed;
  z-index: 2;
  width: 100%;
  height: 70px;
  top: 0;

  &-wrapper {
    display: flex;
    align-items: center;
    background-color: #282828;
    justify-content: space-between;
  }
  
  &__logo { 
    width: 70px;
    height: 70px;
  }
  
}
